import React from "react"

import Icons from "../components/Icons"

import emailjs from "emailjs-com"

class ContactForm extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      name: "",
      email: "",
      message: "",
    }
  }

  sendMessage(event) {
    event.preventDefault()
    if (!this.validateMail()) {
      return
    }
    const templateParams = {
      from_name: this.state.name + " (" + this.state.email + ")",
      to_name: "thomas@allto.digital",
      message: this.state.message,
    }
    emailjs
      .send("webmail", "thomas@allto.digital", templateParams, {
        user_OT4Z9rb1ENe582IVmyysq,
      })
      .then(
        function(response) {
          toast.success("Your message has successfully sent!", {
            position: toast.POSITION.BOTTOM_CENTER,
          })
          console.log("SUCCESS!", response.status, response.text)
        },
        function(err) {
          toast.error("Your message was not able to be sent")
        }
      )
    this.setState({
      name: "",
      email: "",
      message: "",
    })
  }
  handleInputChange(event) {
    event.preventDefault()
    const target = event.target
    const name = target.name
    const value = target.value

    this.setState({ [name]: value })
  }
  render() {
    return (
      <>
        <form
          id={this.props.id}
          data-netlify="true"
          name="contato"
          method="POST"
          action={(this.props.action, "/obrigado")}
          enctype="multipart/form-data"
        >
          <input type="hidden" name="form-name" value="contato" />

          <div className={"container"}>
            <div className={"input_form"}>
              <input
                type="text"
                name="Nome"
                id="name"
                onChange={this.handleInputChange.bind(this)}
                placeholder="Seu nome"
                required
                value={this.state.name}
              />
              <input
                type="email"
                name="Email"
                id="email"
                onChange={this.handleInputChange.bind(this)}
                placeholder="Email"
                required
                value={this.state.email}
                error={this.state.errors.email}
              />
              <textarea
                name="Mensagem"
                id="message"
                onChange={this.handleInputChange.bind(this)}
                placeholder="Escreva sua mensagem"
                required
                value={this.state.feedback}
              />
            </div>
            <div className={"data_form"}>
              <h2>Contate-nos!</h2>
              <p>
                Gostaríamos muito de ouvir o que você tem a dizer.
                <br />
                Entraremos em contato com você dentro de 24 horas!
                <br />
                Fique ligado na sua caixa de emails.
              </p>
              <ul className={"social"}>
                <li>
                  <a href="https://fb.me/allto.digital" title={"Facebook"}>
                    <Icons.Facebook />
                  </a>
                </li>
                <li>
                  <a
                    href="https://www.instagram.com/allto.digital/"
                    title={"Instagram"}
                  >
                    <Icons.Instagram />
                  </a>
                </li>
                <li>
                  <a
                    href="https://www.linkedin.com/company/allto-digital"
                    title={"Linedkin"}
                  >
                    <Icons.Linkedin />
                  </a>
                </li>
              </ul>
            </div>
          </div>
          <button
            id="btn_send"
            type="submit"
            onClick={this.sendMessage.bind(this)}
          >
            <span class="noselect">Enviar</span>
            <div id="circle" />
          </button>
        </form>
      </>
    )
  }
}

export default ContactForm
